<template>
    <div class="header">
        <div class="header_logo">
            <img src="../assets/logo_4.png" alt="">
            <img src="../assets/logo_3.png" alt="">
        </div>


        <div class="header_nav">
            <a href="/main" v-bind:class="{header_nav_active : name == 'home'}">Home</a>
            <a href="/periodicals" v-bind:class="{header_nav_active : name == 'periodicals'}">All periodicals</a>
            <a href="/reader" v-bind:class="{header_nav_active : name == 'reader'}">For reader</a>
            <a href="#footer" @click="footerHere" v-bind:class="{header_nav_active : name == 'contact'}">Contact us</a>
        </div>

        <a v-bind:href="'/employee/'+getId">
        <div class="header_person">
            <div class="header_person_1">
                <img src="../assets/face.png" alt="face">
            </div>

            <div class="header_person_2">
                <h1>{{ getFirstName }} {{ getLastName }}</h1>
                <p>{{ getJob }}</p>
            </div>
        </div>
        </a>


        <div class="header_notification" @click="i=!i">
            <img src="../assets/notification.png" alt="notification">
            <span>{{ countX }}</span>
        </div>


        <div class="header_notification_block" v-if="i">
            <h1>Notifications</h1>
            <div v-for="(item,index) in notifications" :key="index">
                <p>{{ item.description }}</p>
                <h1>{{ item.name }}</h1><span><router-link v-bind:to="item.link">Open</router-link></span>
                <h2>{{ item.unixtime | unixtime }} ago</h2>
            </div>
        </div>
    </div>
</template>



<script>
export default {
    name: 'Header',
    props:{
        name: String
    },
    data(){
        return{
            countX: 0,
            i: false,
            notifications: [],
            checkReading: [],
        }
    },
    methods:{
        footerHere(){
            let a = document.querySelector("#footer")
            if(a.style.top == "80px"){
                a.style.top = "calc(100vh - 110px)"
                a.style.height = "110px"
            }
            else{
                a.style.top = "80px"
                a.style.height = "calc(100vh - 80px)"
                this.name = "contact"
            }
        }
    },
    computed:{
        getFirstName(){
      return localStorage.getItem("userFirstName")
    },
    getLastName(){
      return localStorage.getItem("userSecondName")
    },
    getJob(){
      return localStorage.getItem("userJob")
    },
    getId(){
      return localStorage.getItem("user")
    },
    },
    filters:{
        unixtime(value){
            let seconds = (parseInt(new Date().getTime()/1000) - value ) 
            var numdays = Math.floor(seconds / 86400);

            var numhours = Math.floor((seconds % 86400) / 3600);

            var numminutes = Math.floor(((seconds % 86400) % 3600) / 60);

            var numseconds = ((seconds % 86400) % 3600) % 60;
            if(numdays > 0){
                return numdays + " days " + numhours + " hours ";
            }
            else if(numhours > 0){
                return numhours + " hours " + numminutes + " minutes ";
            }
            else if(numminutes > 0){
                return numminutes + " minutes " + numseconds + " seconds";
            }
        }
    },
    mounted() {
        const axios = require("axios");
        const self = this
        axios.get("http://localhost:8080/notification/"+this.getId,{
            employee_id: this.getId
        })
            .then(function (response) {
                self.countX = response.data[0].c
            })
            .catch(function (error) {
                console.log(error);
            })

        axios.get("http://localhost:8080/notifications/"+this.getId,{
            employee_id: this.getId
        })
            .then(function (response) {
                self.notifications = response.data
            })
            .catch(function (error) {
                console.log(error);
            })

        
    },
}
</script>

