<script>
// @ is an alias to /src
import Header from "@/components/Header";
import Footer from "@/components/Footer";
import Search from "@/components/Search";
import { Carousel, Slide } from 'vue-carousel';




export default {
  name: 'Home',
  data(){
    return{
      items: [],
      userA: [],
      mostPopular_id: 0,
      mostPopular: [],
      max: 0,
    }
  },
  components: {
    Header,
    Footer,
    Search,
    Carousel,
    Slide,
  },
  methods:{

  },
  computed:{
    getFirstName(){
      return localStorage.getItem("userFirstName")
    },
    getLastName(){
      return localStorage.getItem("userSecondName")
    },
    getJob(){
      return localStorage.getItem("userJob")
    },
    getId(){
      return localStorage.getItem("user")
    },
  },

  mounted() {
    const axios = require("axios");
        const self = this
        axios.get("http://localhost:8080/periodicalsall")
            .then(function (response) {
                self.items = response.data
            })
            .catch(function (error) {
                console.log(error);
            })

        axios.get("http://localhost:8080/selectUpdated")
            .then(function (response) {
                self.mostPopular_id = response.data[0]
            })
            .catch(function (error) {
                console.log(error);
            })
        
        setTimeout(()=> {
          axios.get("http://localhost:8080/periodicals/" + self.mostPopular_id.periodical_id_1)
            .then(function (response) {
                self.mostPopular = response.data
                console.log(self.mostPopular)
            })
            .catch(function (error) {
                console.log(error);
            })  


        }, 400)

      var x = setInterval(function() {
      
      let value = self.mostPopular_id.order_date - parseInt(new Date().getTime()/1000)
      
      var hours = Math.floor((value % (1000 * 60 * 60 * 24)) / (60 * 60));
      var minutes = Math.floor((value % ( 60 * 60)) / ( 60));
      var seconds = Math.floor((value % (60)));
    
    
      document.querySelector("#timer").innerHTML = hours + " : " + minutes + " : " + seconds;
      if (value < 0) {
        clearInterval(x);
        axios.get("http://localhost:8080/deleteUpdated")
            .then(function (response) {
                console.log(response)
            })
            .catch(function (error) {
                console.log(error);
            })  
        location.reload();
        }


      }, 1000);



      
        
  },
}
</script>

