<template>
    <div class="employee">
        <Header name="home"/>
        <div class="employee_inner">
            <a @click="logout" class="employee_inner_logout">Logout</a>
            <div class="employee_inner_1">
                <img src="../assets/face.png" alt="face">
                <h1>{{ getFirstName }} {{ getLastName }}</h1>
                <h2>{{ getJob }}</h2>
                <div class="employee_inner_statistics">
                    <span class="employee_inner_statistics_1">{{ count }} periodicals read</span> 
                    <div></div>
                    <span class="employee_inner_statistics_2">{{ countDeadline }} deadlines</span>
                </div>
            </div>


            <div class="employee_queue" v-if="getJob != 'librarian'">
                <h1>History</h1>
                <div class="book_queue_1">
                    <span>Name</span>
                </div>

                <div class="book_queue_2">
                    <span>Status</span>
                </div>
            
                <div class="book_queue_3">
                    <span>Time left</span>
                </div>
                <p v-if="queue.length == 0">No data</p>



                <div class="employee_queue_list">
                <div v-for="(item, index) in queue" :key="index">
                    <div >
                    <div class="book_queue_1">
                        <p>{{ item.name }}</p>
                    </div>
                    <div class="book_queue_2">
                        <p class="book_queue_2_status" v-bind:class="{book_queue_2_status_pending : item.status == 'done'}">{{ item.order_status }} </p>
                    </div>
                    <div class="book_queue_3">
                        <p class="book_queue_2_status" v-bind:class="{book_queue_2_status_pending : item.status == 'done'}" v-if="item.order_status == 'updated'">none </p>
                        <p class="book_queue_2_status" v-bind:class="{book_queue_2_status_pending : item.status == 'done'}" v-if="item.order_status == 'pending'">{{ item.order_status }} </p>
                        <p class="book_queue_2_status" v-bind:class="{book_queue_2_status_pending : item.status == 'done'}" v-if="item.order_status == 'reading'">{{ item.order_date | unixtime }} </p>
                        <p class="book_queue_2_status" v-bind:class="{book_queue_2_status_pending : item.status == 'done'}" v-if="item.order_status == 'sent'">{{ item.order_date | unixtime }} </p>
                        <p class="book_queue_2_status" v-bind:class="{book_queue_2_status_pending : item.status == 'done'}" v-if="item.order_status == 'done'">{{ item.order_status }} </p>
                    </div>

                    </div>
                </div>
                </div>




            </div>
        </div>
        <Footer />
    </div>
</template>

<script>
import Header from "@/components/Header"
import Footer from "@/components/Footer"

export default {
    name: 'Employee',
    data(){
        return{
            queue: [],
            count: 0,
            countDeadline: 0,
        }
    },
    components: {
        Footer, 
        Header, 
    },
    filters:{
        unixtime(value){
            let seconds = (parseInt(new Date().getTime()/1000) - value - 259200) * (-1)
            var numdays = Math.floor(seconds / 86400);

            var numhours = Math.floor((seconds % 86400) / 3600);

            var numminutes = Math.floor(((seconds % 86400) % 3600) / 60);

            var numseconds = ((seconds % 86400) % 3600) % 60;
            if(numdays > 0){
                return numdays + " days " + numhours + " hours ";
            }
            else if(numhours > 0){
                return numhours + " hours " + numminutes + " minutes ";
            }
            else if(numminutes > 0){
                return numminutes + " minutes " + numseconds + " seconds";
            }
        }
    },
    methods: {
        logout(){
            localStorage.removeItem("user")
            localStorage.removeItem("userFirstName")
            localStorage.removeItem("userSecondName")
            localStorage.removeItem("userJob")
            this.$router.push({ path: '/logout' })
        }
    },
    computed:{
        getFirstName(){
      return localStorage.getItem("userFirstName")
    },
    getLastName(){
      return localStorage.getItem("userSecondName")
    },
    getJob(){
      return localStorage.getItem("userJob")
    },
    getId(){
      return localStorage.getItem("user")
    },
    },
    mounted() {
        const self = this
        const axios = require("axios")
        axios.get("http://localhost:8080/order/"+this.getId,{
          employee_id: this.getId,
        })
        .then(function (response) {
                console.log(response)
                self.count = response.data[0].c
            })
            .catch(function (error) {
                alert(error);
            })



        axios.get("http://localhost:8080/orders/"+this.getId,{
          employee_id: this.getId,
        })
        .then(function (response) {
                console.log(response)
                self.queue = response.data
            })
            .catch(function (error) {
                alert(error);
            })
        

        axios.get("http://localhost:8080/deadlines/"+this.getId,{
          employee_id: this.getId,
        })
        .then(function (response) {
                console.log(response)
                self.countDeadline = response.data[0].c
            })
            .catch(function (error) {
                alert(error);
            })
    },
}
</script>

