<template>
    <div class="periodicals">
        <Header name="periodicals"/>
        <h1>Periodicals database</h1>
        <h2>All periodicals all over time. </h2>
        <Search />


        <div class="periodicals_elements">
            <div v-for="(item, index) in items" :key="index">
                <a v-bind:href="'/periodical/'+item.periodical_id">
                    <div class="periodicals_element">
                        <img v-bind:src="item.url_image" alt="">
                    </div>
                </a>
            </div>

            <p v-show="items.length == 0">Not found</p>
        </div>
        <Footer />
    </div>
</template>

<script>
import Header from "@/components/Header";
import Footer from "@/components/Footer";
import Search from "@/components/Search";


export default {
    name: 'SearchView1',
    data(){
        return{
            items: [
            ]
        }
    },
    components:{
        Footer,
        Header,
        Search
    },
    mounted(){
        const axios = require("axios");
        const self = this
            axios.get("http://localhost:8080/periodicalsauthor/"+this.$route.params.title)
            .then(function (response) {
                self.items = response.data
            })
            .catch(function (error) {
                console.log(error);
            })

    }
}
</script>

