import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import Reader from '../views/Reader.vue'
import Periodicals from '../views/Perodicals.vue'
import Book from '../views/Book.vue'
import ReadNow from '../views/ReadNow.vue'
import Employee from '../views/Employee.vue'
import addbook from '../views/AddBook.vue'
import SearchView from '../views/SearchView.vue'
import SearchView1 from '../views/SearchView1.vue'
import SearchView2 from '../views/SearchView2.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Login',
    component: Login,
    meta:{
      title: "Library - Login",
      guest: true
    }
  },
  {
    path: '/main',
    name: 'Home',
    component: Home,
    meta:{
      title: "Library - Main",
      requiresAuth: true
    }
  },
  {
    path: '/reader',
    name: 'Reader',
    component: Reader,
    meta:{
      title: "Library - Reader",
      requiresAuth: true
    }
  },
  {
    path: '/periodicals',
    name: 'Periodicals',
    component: Periodicals,
    meta:{
      title: "Library - Reriodicals",
      requiresAuth: true
    }
  },
  {
    path: '/periodical/:id',
    name: 'Book',
    component: Book,
    meta:{
      title: "Library - Periodical",
      requiresAuth: true
    }
  },
  {
    path: '/readnow',
    name: 'ReadNow',
    component: ReadNow,
    meta:{
      title: "Library - read now",
      requiresAuth: true
    }
  },
  {
    path: '/employee/:id',
    name: 'Employee',
    component: Employee,
    meta:{
      title: "Library - Employee",
      requiresAuth: true
    }
  },
  {
    path: '/add',
    name: 'addbook',
    component: addbook,
    meta:{
      title: "Library - add book",
      requiresAuth: true
    }
  },
  {
    path: '/searchn',
    name: 'searchn',
    component: SearchView,
    meta:{
      title: "Library - Search",
      requiresAuth: true
    }
  },
  {
    path: '/searcha',
    name: 'searcha',
    component: SearchView1,
    meta:{
      title: "Library - Search",
      requiresAuth: true
    }
  },
  {
    path: '/searchna',
    name: 'searchna',
    component: SearchView2,
    meta:{
      title: "Library - Search",
      requiresAuth: true
    }
  },
  { 
    path: '/logout', 
    redirect: '/' 
  }
  
  

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})


router.beforeEach((to, from, next) => {
  if(to.matched.some(record => record.meta.requiresAuth)) {
    if (localStorage.getItem('user') == null) {
      next({
        path: '/',
        params: { nextUrl: to.fullPath }
      })
    } 
    
  } else if(to.matched.some(record => record.meta.guest)) {
    if(localStorage.getItem('user') == null){
      next()
    }
    else{
      next({ name: 'Home'})
    }
  }else {
    next() 
  }






  const nearestWithTitle = to.matched.slice().reverse().find(r => r.meta && r.meta.title);
  const nearestWithMeta = to.matched.slice().reverse().find(r => r.meta && r.meta.metaTags);
  const previousNearestWithMeta = from.matched.slice().reverse().find(r => r.meta && r.meta.metaTags);
  if(nearestWithTitle) {
    document.title = nearestWithTitle.meta.title;
  } else if(previousNearestWithMeta) {
    document.title = previousNearestWithMeta.meta.title;
  }
  Array.from(document.querySelectorAll('[data-vue-router-controlled]')).map(el => el.parentNode.removeChild(el));
  if(!nearestWithMeta) return next();
  nearestWithMeta.meta.metaTags.map(tagDef => {
    const tag = document.createElement('meta');

    Object.keys(tagDef).forEach(key => {
      tag.setAttribute(key, tagDef[key]);
    });

    tag.setAttribute('data-vue-router-controlled', '');

    return tag;
  })
      .forEach(tag => document.head.appendChild(tag));
  next();
});


export default router
