<template>
    <div class="book">
        <Header name="periodicals" />
        
        <div class="home_special_offer">
            <div class="home_special_offer_1">
                <img v-bind:src="book.url_image" alt="image_4">
            </div>

            <div class="home_special_offer_2">
                <h2>SPECIAL ISSUE</h2>
                <strong>{{ book.name }}</strong>
                <h3>{{ book.publisher }}  &#183; {{ book.year }}</h3>
                <h1>Annotation</h1>
                <p>{{ book.description }}</p>
                <button v-if="getJob == 'librarian'">Deactivate</button>
                <button class="home_special_offer_2_btn" v-show="queue2 && queue2[1].employee_id == getId && i == 1" @click="confimRec">Confirm receiving</button>
                <button class="home_special_offer_2_btn" v-if="btn == 1" @click="sendPer">Send</button>
            </div>
        </div>

        <div class="book_queue">
            <h1>Queue</h1>
            <div class="book_queue_1">
                <span>Name</span>
            </div>

            <div class="book_queue_2">
                <span>Status</span>
            </div>
            
            <div class="book_queue_3">
                <span>Time left</span>
            </div>
            <p v-show="queue.length == 0">No data</p>
            <div class="book_queue_list">
            <div v-for="(item, index) in queue" :key="index" class="book_queue_element">
                <div class="book_queue_1" v-if="item.order_status == 'sent' || item.order_status == 'reading' || item.order_status == 'pending'">
                    <p v-if="item.order_status != 'updated'">{{ item.first_name }}  {{ item.last_name }} </p>
                </div>
                <div class="book_queue_2" v-if="item.order_status != 'updated' ">
                    <input type="hidden" v-bind:value="item.order_id" v-if="item.order_status == 'sent' || item.order_status == 'reading' || item.order_status == 'pending'">



                    <p v-if="item.order_status == 'reading'"  class="book_queue_2_status" v-bind:class="{book_queue_2_status_pending : item.order_status == 'reading' && id == item.employee_id}"><span>{{ item.order_status }} </span></p>
                    <p v-if="id == item.employee_id && item.order_status == 'pending'" class="book_queue_2_status" v-bind:class="{book_queue_2_status_pending_2 : item.order_status == 'pending' && id == item.employee_id}" style="cursor: pointer" @click="statusDelete"><span>{{ item.order_status }} </span></p>
                    <p v-if="id != item.employee_id && item.order_status == 'pending'" class="book_queue_2_status" v-bind:class="{book_queue_2_status_pending : item.order_status == 'pending' && id == item.employee_id}"><span>{{ item.order_status }} </span></p>
                    <p v-if="item.order_status == 'sent'"  class="book_queue_2_status" v-bind:class="{book_queue_2_status_pending : item.order_status == 'sent' && id == item.employee_id}"><span>{{ item.order_status }} </span></p>


            

                </div>
                <div class="book_queue_3" v-if="item.order_status == 'sent' || item.order_status == 'reading' || item.order_status == 'pending'">
                    <p class="book_queue_2_status" v-bind:class="{book_queue_2_status_pending : item.order_status == 'pending'}" v-if="item.order_status == 'pending' && item.order_status != 'updated'">{{ item.order_status }} </p>
                    <p class="book_queue_2_status" v-bind:class="{book_queue_2_status_pending : item.order_status == 'pending'}" v-if="item.order_status != 'pending' && item.order_status != 'updated'">{{ item.order_date | unixtime }} </p>


                </div>

            </div>
            </div>

            <p class="book_queue_btn" @click="takePlace" v-if="permission == 0 && getJob != 'librarian'">Take a place</p>
        </div>



        <div class="book_process">
            <span class="book_process_1" v-bind:class="{book_process_1_active: i == 0}">1</span>
            <div class="book_process_dotted"></div>
            <span class="book_process_1" v-bind:class="{book_process_1_active: i > 0}">2</span>
            <br>
            <span class="book_process_statement book_process_statement_1" v-bind:class="{book_process_statement_active: i == 0}">Waiting for send</span>
            <span class="book_process_statement book_process_statement_3" v-bind:class="{book_process_statement_active: i > 1}">Waiting for withdraw</span>

        </div>
        <Footer />
    </div>
</template>


<script>
import Header from "@/components/Header"
import Footer from "@/components/Footer"
export default {
    name: 'Book',
    data(){
        return{
            i: 0,
            book: [],
            queue: [],
            queue2: [1,1],
            max: 0,
            permission: 0,
            btn: 0,
            id: localStorage.getItem("user")
        }
    },
    filters:{
        unixtime(value){
            let seconds = (parseInt(new Date().getTime()/1000) - value - 259200) * (-1)
            var numdays = Math.floor(seconds / 86400);

            var numhours = Math.floor((seconds % 86400) / 3600);

            var numminutes = Math.floor(((seconds % 86400) % 3600) / 60);

            var numseconds = ((seconds % 86400) % 3600) % 60;
            if(numdays > 0){
                return numdays + " days " + numhours + " hours ";
            }
            else if(numhours > 0){
                return numhours + " hours " + numminutes + " minutes ";
            }
            else if(numminutes > 0){
                return numminutes + " minutes " + numseconds + " seconds";
            }
        }
    },
    computed:{
        getFirstName(){
      return localStorage.getItem("userFirstName")
    },
    getLastName(){
      return localStorage.getItem("userSecondName")
    },
    getJob(){
      return localStorage.getItem("userJob")
    },
    getId(){
      return localStorage.getItem("user")
    },
    },
    methods: {
        confimRec(){
            const axios = require("axios")
            axios.get("http://localhost:8080/updateConfirmRec/"+this.queue2[1].order_id, {
                order_id: this.queue2[1].order_id
            })
            .then(function (response) {
                console.log(response)
            })
            .catch(function (error) {
                console.log(error);
            })
            .then(function(){
                location.reload()
            })
            
        },
        sendPer(){
            const axios = require("axios")
            axios.get("http://localhost:8080/updateSendBtn/"+this.$route.params.id+"/"+localStorage.getItem("user"), {
                periodical_id: this.$route.params.id,
                employee_id: localStorage.getItem("user")
            })
            .then(function (response) {
                console.log(response)
            })
            .catch(function (error) {
                console.log(error);
            })
            .then(function(){
                location.reload()
            })
        },
        takePlace(){
            let a = '';
            if(this.queue.length == 0){
                a = 'reading'
                this.max = parseInt(new Date().getTime()/1000)
            }
            else{
                a = 'pending'
                this.max = parseInt(new Date().getTime()/1000) + 259200
            }
            const axios = require("axios")
            axios.post("http://localhost:8080/order/" + this.$route.params.id, {
                periodical_id_1: Number.parseInt(this.$route.params.id),
                employee_id: Number.parseInt(localStorage.getItem("user")),
                order_status: a,
                order_date: this.max
            })
            .then(function (response) {
                console.log(response)
            })
            .catch(function (error) {
                console.log(error);
            })
            .then(function(){
                location.reload();
            })

        },
        statusDelete(event){
            if(event.target.childNodes[0].innerHTML == 'SURE?'){
                const axios = require("axios")
                let a = event.target.parentNode.parentNode.childNodes[0].value
                let b = parseInt(new Date().getTime()/1000) + 10800
                axios.put("http://localhost:8080/order/"+a+"/"+ b, {
                    order_date: b,
                    order_id: a
                })
                .then(function (response) {
                    console.log(response)
                })
                .catch(function (error) {
                    console.log(error);
                })
                .then(function(){
                    location.reload();
                })


                /*
                axios.get("http://localhost:8080/proc/"+ a)
                .then(function (response) {
                    console.log(response)
                })
                .catch(function (error) {
                    console.log(error);
                })
                */
            }
            else{
                event.target.childNodes[0].innerHTML = 'SURE?'
                event.target.style.backgroundColor = '#DA1E28'
                event.target.classList.remove("book_queue_2_status_pending_2")
            }
        }
    },
    components:{
        Header, 
        Footer
    },
    mounted() {
    },
    created(){
        const axios = require("axios")
        const self = this
        axios.get("http://localhost:8080/periodicals/" + this.$route.params.id)
            .then(function (response) {
                self.book = response.data
            })
            .catch(function (error) {
                console.log(error);
            })
        
        axios.get("http://localhost:8080/orderP/" + this.$route.params.id)
            .then(function (response) {
                self.queue = response.data
                console.log(response)
            })
            .catch(function (error) {
                console.log(error);
            })
            .then(function(){
                for(let i = 0; i < self.queue.length; i++){
                    if(self.queue[i].employee_id == localStorage.getItem("user")){
                        self.permission = 1;
                    }
                    if(self.queue[i].employee_id == localStorage.getItem("user") && self.queue[i].order_status == 'reading'){
                        self.btn = 1;
                    }
                }
            })

        axios.get("http://localhost:8080/orderP2/" + this.$route.params.id)
            .then(function (response) {
                self.queue2 = response.data
                console.log(response)
            })
            .catch(function (error) {
                console.log(error);
            })
            





        axios.get("http://localhost:8080/maxTime/"+ this.$route.params.id, {
            periodical_id: this.$route.params.id
        })
            .then(function (response) {
                self.max = response.data[0].c
            })
            .catch(function (error) {
                console.log(error);
            })

        axios.get("http://localhost:8080/selectAll/"+this.$route.params.id+"/"+localStorage.getItem("user"), {
            periodical_id: this.$route.params.id,
            employee_id: localStorage.getItem("user")
        })
            .then(function (response) {
                console.log(response)
            })
            .catch(function (error) {
                console.log(error);
            })

        axios.get("http://localhost:8080/selectStatus/"+this.$route.params.id, {
            periodical_id: this.$route.params.id,
        })
            .then(function (response) {
                self.i = response.data[0].c
            })
            .catch(function (error) {
                console.log(error);
            })

            





        
    }
}
</script>


