<template>
    <div class="addbook">
        <Header name="periodicals" />
        <div class="addbook_offer">
            <div class="home_special_offer_1">
                <img src="../assets/background.png" alt="image_4">
                <div class="home_special_offer_1_text">
                    <!--
                    <input @change="changeName" type="file" name="img" id="input" accept="image/*" size="10" value="Choose file">
                    <strong @click="takeFile">Choose file</strong>
                    -->
                    <input type="text" placeholder="Link of image" v-model="link">
                    <span>{{ imgName }}</span>
                    <p>Files with PNG, JPG, GIF formats with 10MB size are available to download.</p>
                </div>
            </div>

            <div class="home_special_offer_2">
                <h2 class="h2">SUBTOPIC</h2>
                <input type="text" class="strong" placeholder="TITLE" v-model="title">
                <input type="text" class="h3" placeholder="Author" v-model="author">  &#183; <input type="number" class="span" placeholder="Year" v-model="year">
                <h1 class="h1">Annotation</h1>
                <textarea type="text" class="p" placeholder="Write annotation" v-model="annotation"></textarea>
                <button class="addbook_save" @click="createPer">Save</button>
                <a href="/main">
                    <button>Cancel</button>
                </a>
            </div>
        </div>
        <Footer />
    </div>
</template>


<script>
import Header from "@/components/Header"
import Footer from "@/components/Footer"


export default {
    name: 'addbook',
    data(){
        return{
            i: 1,
            imgName: '',
            link: "",
            title: '',
            author: '',
            year: '',
            annotation: '',
        }
    },
    methods:{
        takeFile(){
            document.querySelector("#input").click()
            this.imgName = document.querySelector("#input").files.item(0).name
        },
        changeName(){
            this.imgName = document.querySelector("#input").files.item(0).name
        },
        createPer(){
            if(this.link && this.title && this.author && this.year && this.annotation){
            const axios = require("axios")
            axios.post("http://localhost:8080/periodicalsall",{
                publisher: this.author,
                name: this.title,
                year: this.year,
                description: this.annotation,
                url_image: this.link
            })
            .then(function (response) {
                alert("success")
                console.log(response);
            })
            .catch(function (error) {
                alert("erro1r");
                console.log(error);
            });
            this.$router.push({ name: 'Periodicals' })
            }
            else{
                alert("Fill all inputs")
            }

        }

    },
    components:{
        Header, 
        Footer
    },
}
</script>


